<template>
  <v-container>
    <v-row>
      <v-window v-model="step" touchless class="flex-fill">
        <v-window-item :value="1">
          <v-card class="phone-input-card d-flex animate__animated animate__fadeInDown">
            <v-spacer></v-spacer>
            <input
                id="your-phone-input"
                ref="phone"
                class="phone-input"
                autocomplete="tel"
                name="phone"
                type="tel"
                placeholder="Ваш телефон"
                v-mask="'+7 (###) ###-##-##'"
                v-model="phone"
                :error="showPhoneError"
                :disabled="loading"
                @paste="onPastePhone"
                @keydown="onKeyInput"
                @beforeinput="onKeyInput"
                @keyup.enter="onKeyEnter($refs.phone)"
            />
            <v-btn
                id="your-phone-button"
                class="mx-1"
                fab
                dark
                large
                :color="showPhoneError ? 'red darken-2' : 'green darken-2'"
                @click="sendPhone"
            >
              <v-icon dark>
                {{showPhoneError ? 'mdi-help' : 'mdi-arrow-right-bold'}}
              </v-icon>
            </v-btn>
          </v-card>
        </v-window-item>
        <v-window-item :value="2">
          <v-card class="code-input-card d-flex animate__animated animate__fadeInTopRight">
            <v-spacer></v-spacer>
            <input
                id="your-code-input"
                ref="code"
                class="phone-input"
                autocomplete="one-time-code"
                name="phone"
                type="tel"
                :placeholder="showCodeWrong ? 'Код из СМС' : 'Код из СМС'"
                v-model="code"
                maxlength="6"
                :disabled="loading"
                @paste="onPastePhone"
                @keydown="onKeyInput"
                @beforeinput="onKeyInput"
                @keyup.enter="onKeyEnter($refs.phone)"
            />
            <v-btn
                id="your-code-button"
                class="mx-1"
                fab
                dark
                large
                :color="showCodeError ? 'red darken-2' : 'green darken-2'"
                @click="sendCode"
            >
              <v-icon dark :class="{infinite__rotation: showWaitingVerification}">
                {{showWaitingVerification ? 'mdi-refresh' : showCodeError ? 'mdi-help' : 'mdi-arrow-right-bold'}}
              </v-icon>
            </v-btn>
          </v-card>
        </v-window-item>
      </v-window>
    </v-row>
    <div id="recaptcha-container"></div>
  </v-container>
</template>

<script>
  import phoneInputMixin from '../mixins/phoneInputMixin'
  import animationsMixin from '../mixins/animationsMixin'
  import firebaseMixin from '../mixins/firebaseMixin'
  import { mask } from '@rj-pkgs/vue-the-mask'

  export default {
    name: 'Login',
    mixins: [phoneInputMixin, animationsMixin, firebaseMixin],
    directives: { mask },

    data: () => ({
      loading: false,
      solved: false,
      step: 1,
      showPhoneError: false,
      showCodeError: false,
      showCodeWrong: false,
      showWaitingVerification: false,
      phone: '',
      code: '',
    }),

    methods: {
      onKeyEnter (target, event) {
        if (this.step === 1) {
          this.sendPhone()
        } else {
          this.sendCode()
        }
      },
      sendPhone () {
        const phoneRaw = this.phone.replace(/[()-]|\s/g, '')
        if (phoneRaw.length !== 12) {
          this.showPhoneError = true
          this.$refs.phone.focus()
          setTimeout(() => this.showPhoneError = false, 1100)
          this.replaceAnimation('.phone-input-card', 'animate__headShake')
        } else {
          this.firebaseSignIn(phoneRaw)
          this.step = 2
          setTimeout(() => this.$refs.code.focus(), 2000)
        }
      },
      sendCode () {
        if (this.showWaitingVerification) {
          return
        }
        if (this.code.length < 6) {
          this.showCodeError = true
          this.$refs.code.focus()
          setTimeout(() => this.showCodeError = false, 1100)
          this.replaceAnimation('.code-input-card', 'animate__headShake')
        } else {
          this.firebaseConfirm(this.code, this.codeConfirmed, this.codeRejected)
          this.showWaitingVerification = true
          setTimeout(() => this.$refs.code.focus(), 2000)
        }
      },
      codeConfirmed (user) {
        const phone = this.phone.replace(/[()-]|\s/g, '')
        const phoneVerification = window.confirmationResult.verificationId
        this.$socket.client.emit('signInSocket', {phone, phoneVerification})
        this.$store.commit('setPhone', {phone, phoneVerification})
        this.$router.replace('/')
      },
      codeRejected () {
        this.showWaitingVerification = false
        this.code = ''
        this.showCodeWrong = true
        this.showCodeError = true
        this.$refs.code.focus()
        setTimeout(() => this.showCodeError = false, 1100)
        this.replaceAnimation('.code-input-card', 'animate__headShake')
      },
    },

    mounted() {
      this.firebaseVerify('recaptcha-container')
      this.$refs.phone.focus()
    },

    deactivate() {
      this.step = 1
      this.showCodeWrong = false
      this.showCodeError = false
      this.showWaitingVerification = false
      this.code = ''
      this.phone = ''
    }
  }
</script>

<style lang="scss">
  .v-sheet.v-card.phone-input-card, .v-sheet.v-card.code-input-card {
    margin: 10vw auto;
    padding: 20px;
    max-width: 350px;
    min-width: 330px;
    border-radius: 50px;
    box-shadow: none !important;
    background: rgba(200,200,200,0.2);
    @supports (-webkit-backdrop-filter: none) or (backdrop-filter: none) {
      backdrop-filter: blur(10px);
    }
    input.phone-input {
      font-size: 22px;
      max-width: 200px;
    }
  }
  #recaptcha-container {
    opacity: 0;
  }
</style>
