export default {
  methods: {
    onPastePhone (event) {
      const clip = (event.originalEvent || event).clipboardData
      const text = (clip ? clip.getData('text/plain') : window.clipboardData.getData('text')) || ''
      if (text.length >= 10 && text.charAt(0) === '8') {
        event.preventDefault()
        event.target.value = (text.length > 10 && text.charAt(0) === '8') ? text.substring(1) : text
        event.target.dispatchEvent(this.newEvent('input'))
      }
      this.showDateErrors = false
    },
    onKeyInput (event) {
      if (event.type === 'keydown' && event.key === 'Unidentified') {
        // mobile device, no keys info, wait for before input event
        this.useInputEvent = true
        return
      }
      if (event.type === 'beforeinput' && !this.useInputEvent) {
        // ignore before input event so we did things on key input
        return
      }
      let end = event.target.selectionEnd
      const start = event.target.selectionStart
      const value = event.target.value
      const isPhone = event.target.name.indexOf('phone') !== -1
      const isDate = event.target.name.indexOf('date') !== -1
      const keySymbol = event.key || event.data
      if (isPhone && this.showPhoneError) {
        this.showPhoneError = false
      }
      if (isDate && this.showDateErrors) {
        this.showDateErrors = false
      }
      // replace '8' to '+7' for phone
      if (isPhone && start === 0 && keySymbol === '8') {
        event.target.value = '+7 (' + value.substr(end + 1)
        event.target.selectionStart = event.target.selectionEnd = Math.max(4, end + 1)
        event.target.dispatchEvent(this.newEvent('input'))
        event.preventDefault()
      } else if (value.length !== end) {
        if (start === end) {
          if (keySymbol >= '0' && keySymbol <= '9' && !event.ctrlKey && !event.shiftKey && !event.altKey && (isDate || isPhone)) {
            while (((isPhone ? end < 4 : false) || value.charAt(end) < '0') && end < value.length) {
              end++
            }
            if ((isDate && value.length === 10) || (isPhone && value.length === 18)) {
              event.target.value = value.substr(0, end) + keySymbol + value.substr(end + 1)
            } else {
              event.target.value = value.substr(0, end) + keySymbol + value.substr(end)
            }
            event.target.selectionStart = event.target.selectionEnd = end + 1
            setTimeout(() => {
              event.target.selectionStart = event.target.selectionEnd = end + 1
            })
            event.target.dispatchEvent(this.newEvent('input'))
            event.preventDefault()
          } else if (keySymbol === 'Backspace') {
            if (isPhone ? end > 4 : end > 0) {
              while (value.charAt(end - 1) < '0') {
                end--
              }
              event.target.value = value.substr(0, end - 1) + value.substr(end)
              event.target.selectionStart = event.target.selectionEnd = end - 1
              setTimeout(() => {
                event.target.selectionStart = event.target.selectionEnd = end - 1
              })
              event.target.dispatchEvent(this.newEvent('input'))
              event.preventDefault()
            } else {
              event.preventDefault()
            }
          } else if (keySymbol === 'Delete' || keySymbol === 'Del') {
            while ((isPhone ? end < 4 : false) || value.charAt(end) < '0') {
              end++
            }
            event.target.value = value.substr(0, end) + value.substr(end + 1)
            event.target.selectionStart = event.target.selectionEnd = end
            setTimeout(() => {
              event.target.selectionStart = event.target.selectionEnd = end
            })
            event.target.dispatchEvent(this.newEvent('input'))
            event.preventDefault()
          } else if (event.keyCode > 48 && event.keyCode < 91 && !event.ctrlKey) {
            event.preventDefault()
          }
        } else if (event.keyCode > 48 && event.keyCode < 91 && !event.ctrlKey) {
          setTimeout(() => {
            event.target.selectionStart = event.target.selectionEnd = start + 1
          }, 100)
        } else if (keySymbol === 'Backspace') {
          setTimeout(() => {
            event.target.selectionStart = event.target.selectionEnd = start - 1
          }, 100)
        } else if (keySymbol === 'Delete' || keySymbol === 'Del') {
          setTimeout(() => {
            event.target.selectionStart = event.target.selectionEnd = start
          }, 100)
        }
      }
    },
    newEvent (name) {
      var evt = document.createEvent('Event')
      evt.initEvent(name, true, true)
      return evt
    },
  }
}
