export default {
  methods: {
    firebaseVerify (recaptchaElementId) {
      window.recaptchaVerifier = new firebase.auth.RecaptchaVerifier(recaptchaElementId, {
        'size': 'invisible',
        'callback': (response) => {
          console.log('solved, verificationCode:', response)
          // reCAPTCHA solved, allow signInWithPhoneNumber.
        }
      });
    },
    firebaseSignIn (phoneRaw, successCallback = null, errorCallback = null) {
      firebase.auth().signInWithPhoneNumber(phoneRaw, window.recaptchaVerifier)
      .then((confirmationResult) => {
        console.log('verificationId:', confirmationResult.verificationId)
        window.confirmationResult = confirmationResult;
        successCallback && successCallback(confirmationResult);
      })
      .catch((error) => {
        // Error; SMS not sent
        console.log(error)
        errorCallback && errorCallback(error);
      });
    },
    firebaseConfirm (code, successCallback = null, errorCallback = null) {
      window.confirmationResult.confirm(code)
      .then(function (result) {
        // User signed in successfully.
        console.log('firebase success', result.user);
        successCallback && successCallback(result.user);
        // ...
      }).catch(function (error) {
        // User couldn't sign in (bad verification code?)
        console.log('firebase error', error);
        errorCallback && errorCallback(error);
        // window.recaptchaVerifier.render();
      });
    },
  }
}
